import React from "react";
import styles from "./styles.module.css";

function FooterAdverstment() {
  return (
    <>
      <div className={styles.footer_addverstment}>
        <div className={styles.footer_persentage}>
          <h3>
            <span>20%</span> OFF
          </h3>
          <p>Car shipping</p>
        </div>
        <div className={styles.footer_buttons}>
          <a className={styles.footer_shipping_btn} href="#">Get Shipping Estimate - Free</a>
          <a className={styles.footer_callus_btn} href="#">Call us</a>
        </div>
      </div>
    </>
  );
}

export default FooterAdverstment;
